.button {
  display: flex;
  align-items: center;
  gap: 10px;
  width: fit-content;
  justify-self: end;
  border: none;
  background: #ff7b00;
  border-radius: 8px;
  padding: 15px 35px;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.button:hover {
  opacity: 0.8;
}

@media screen and (min-width: 769px) {
  .button {
    grid-column: 2 / -1;
  }
}
